import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";

    const ErrorMsg = (props) => {

    const [isTwitter,setisTwitter]=useState(false)

    useEffect(() => {
        
        if(!isTwitter){
            if(props.connectedAccounts && props.connectedAccounts){
                const connectAccount = props.connectedAccounts
                if(connectAccount && Object.keys(connectAccount).length){
                    const filterItem = connectAccount.some(item => item.ConnectedAccounts.networkId === 1 && item.ConnectedAccounts.is_verified==0);
                    if(filterItem) setisTwitter(filterItem)
                }
            }
        }

    }, []);

 
    return (
          <>
          { 
            isTwitter ? 
            <div class="review-bnn position-relative">
                Refresh Your Twitter Wall To Ensure Smooth Content Sync. <Link to={`/transactions#connectedAccounts`}> <span class="rbnn-clicable">Click here to do it now!</span> </Link> 
            </div> : null } </>
        );
    };

 export default React.memo(ErrorMsg)